import '@toast-ui/editor/dist/toastui-editor-viewer.css';
import { useRef, useState, useEffect } from 'react';
import {Checkboxinput, Typeheader, Selectwrapper} from '../components/style';
import styled from 'styled-components'

const Completebutton = styled.button`
  display: block;
  margin: 5px;
  margin-right: 3px;
  margin-left: 10px; 
  font-weight: 600;
  border: 2px solid black;
  border-radius: 5px;
  cursor: pointer;
  background-color: #ffffff;
`

export default function HeaderColor(props) {
  const [headtext, setHeadext] = useState("");
  const [text, setText] = useState("");

  const changeheader = (e) => {
    setHeadext(e.target.value)
  }

  const changetext = (e) => {
    let a = e.target.value.replaceAll("-", "<li>")
    setText(a.replaceAll(/\n/g, "</li>"))
  }

  const textchange = ()=>{
    props.setMyintroheader(headtext);
    props.setMyintro(text);
  }

  return (
    <Selectwrapper>
                    <Typeheader>1.    .</Typeheader>
                    <Checkboxinput type="text" placeholder=' '  onChange={changeheader}/>
                    <Typeheader>2.   .</Typeheader>
                    <textarea placeholder=' 
                    -   .
                    ex) -       .'  onChange={changetext} style={{"margin": "5px",
    "margin-right": "3px",
    "margin-left": "10px", "width": "250px", "height": "150px"}}/>

                      <Completebutton onClick={textchange}>  </Completebutton>
              
                </Selectwrapper>
  )
}
